.info{
    .intro{
        width: 100%;
        height: 60vh;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        position: relative;
        &:before, &:after{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
        &:before{
            z-index: -2;
            background: center url("../img/info-intro.jpg");
            background-size: cover;

        }
        &:after{
            z-index: -1;
            background: linear-gradient(to top, rgba(0,0,0,0.40), rgba(0,0,0,0.40));
        }
        &__title{
            font-size: 102px;
            letter-spacing: 1px;
            color: white;
            margin: auto;
        }
        &__arrow{
            width: 40px;
            height: 40px;
            margin-bottom: 50px;
            display: flex;
            &-icon{
                fill: white;
                width: 100%;
                transform: rotate(90deg);
                transform-origin: 0;
            }
        }
    }
    .pay{
        padding: 150px 0 75px;
        &__wrapper{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            &-title {
                font-weight: 300;
                font-size: 42px;
                line-height: 1.23;
                margin-bottom: 105px;
            }
            &-table{
                margin: 0 auto;
                width: 960px;
                table-layout: fixed;
                border-collapse: collapse;
                overflow:auto;
                td, th{
                    text-align: center;
                    padding: 22px 20px;
                }
                td:nth-child(1),
                th:nth-child(1){
                    text-align: left;
                }
                td:nth-child(2),
                td:nth-child(3){
                    border-left: 1px solid #e3e3e3;
                }
                tr:nth-child(2n){
                    background-color: #f5f5f5;
                }
                tr:nth-child(2n - 1){
                    background-color: #eeeeee;
                }
                thead tr{
                    background-color: inherit !important;
                }


            }
        }
    }
    .delivery{
        padding: 150px 0 75px;
        &__wrapper{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            &-title {
                font-weight: 300;
                font-size: 38px;
                line-height: 1.23;
                margin-bottom: 40px;
            }
            &-text {
                max-width: 560px;
                text-align: center;
                font-weight: 300;
                font-size: 22px;
                line-height: 1.5;
                margin-bottom: 105px;
            }
            &-info{
                display: flex;
                width: 100%;
                align-items: center;
                justify-content: space-around;
                &-item{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    h4{
                        font-size: 20px;
                        line-height: 1.35;
                        font-weight: 400;
                        margin: 0;
                        margin-bottom: 10px;
                    }
                    p{
                        margin: 0;
                        font-size: 16px;
                        line-height: 1.35;
                        font-weight: 300;
                    }
                }
                .single-chart {
                    width: 160px;
                    justify-content: space-around ;
                }

                .circular-chart {
                    display: block;
                    margin: 10px auto;
                }

                .circle-bg {
                    fill: none;
                    stroke: #eee;
                    stroke-width: 3.8;
                }

                .circle {
                    fill: none;
                    stroke-width: 2.8;
                    stroke-linecap: round;
                    stroke: #ffea73;
                }
                .percentage {
                    color: #000;
                    font-weight: 400;
                    font-size: 0.4em;
                    text-anchor: middle;
                }
            }
        }
    }
    .question{
        padding: 150px 0 75px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        &-title{
            font-size: 62px;
            text-align: center;
            font-weight: 300;
            max-width: 600px;
            margin-bottom: 100px;
        }
        &__wrapper{
            padding: 90px 0;
            background: #eeeeee;
            width: 100%;
            .accordion {
                padding: 40px 20px 0px;
                max-width: 620px;
                margin: 0 auto;
                &__item{
                    padding: 25px 30px;
                    background: white;
                    margin-bottom: 1px;
                }
            }
            .accordion__title {
                display: flex;
                justify-content: space-between;
                align-items: center;
                position: relative;
                cursor: pointer;
                h3{
                    margin: 0;
                    font-size: 22px;
                    font-weight: 400;
                    color: #000;
                    line-height: 1.35;
                }
            }
            .accordion__icon {
                width: 16px;
                height: 16px;
                background-image: url(https://image.flaticon.com/icons/svg/748/748122.svg);
                background-repeat: no-repeat;
                transform: rotate(-45deg);
                transition: all .4s ease-in-out;
            }
            .accordion__content {
                max-height: 0;
                overflow: hidden;
                transition: all .6s ease-in-out;
            }
            .accordion__content p {
                font-size: 14px;
                line-height: 1.55;
                font-weight: 300;
                color: #000;
            }
            .active .accordion__content {
                max-height: 150px;
            }
            .active .accordion__icon {
                transform: rotate(0deg);
            }
        }
    }
}


@media (max-width: 1260px) {
    .info{
        .intro__title{
            font-size: calc(18px + 5vw);
        }
    }
}
@media (max-width: 980px) {
    .info{
        .delivery__wrapper-info{
            flex-direction: column;
            &-item{
                margin-bottom: 20px;
            }
        }
        .question-title{
            font-size: 44px;
        }
    }
}